<template>
  <div>
    <h1>菜单</h1>
    <hr>
    <table class="table">

      <tr>
        <th>品种</th>
        <th>删除</th>
      </tr>

      <tr
        v-for="(item,index) in menu"
        :key='index'
      >
        <td>{{item.name}}</td>
        <td>
          <span @click="deleteItem(index)">x</span>
        </td>
      </tr>

    </table>
  </div>
</template>

<script>
export default {
  props: {
    menu: Object
  },
  methods: {
    deleteItem(id) {
      this.$axios
        .delete(`/menu/${id}.json`)
        .then(res => {
          console.log(res);
          this.$emit("update");
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
td span {
  display: inline-block;
  width: 20px;
  height: 20px;
  text-align: center;
  border: 1px solid red;
  color: red;
  cursor: pointer;
  line-height: 20px;
}
</style>