<template>
  <div>

    <div class="box">

      <AddPizza
        @update='update'
        class="add"
      ></AddPizza>
      <Menu
        @update='update'
        class="menu"
        :menu='menu'
      ></Menu>
    </div>
  </div>
</template>

<script>
import AddPizza from "../components/AddNewPizza";
import Menu from "../components/Menu";
export default {
  components: {
    AddPizza,
    Menu
  },
  data() {
    return {
      menu: {}
    };
  },
  methods: {
    fetchMenu() {
      this.$axios.get("/menu.json").then(res => {
        console.log(res.data);
        this.$store.dispatch("asyncmenu", res.data);
        this.menu = this.$store.getters.getmenu;
      });
      console.log("-------------" + this.menu);
    },
    update() {
      this.fetchMenu();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fetchMenu();
    });
  }
};
</script>

<style scoped>
.box {
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
.add {
  width: 60%;
}
.menu {
  width: 30%;
}
</style>