<template>
  <div>
    <h1>添加新的Pizza</h1>

    <Minput
      type='text'
      text='品种'
      v-model="pizza.name"
    ></Minput>
    <Minput
      type='text'
      text='描述'
      v-model="pizza.description"
    ></Minput>
    <h4>选项一</h4>
    <Minput
      type='text'
      text='尺寸'
      v-model="pizza.options[0].size"
    ></Minput>
    <Minput
      type='text'
      text='价格'
      v-model="pizza.options[0].price"
    ></Minput>
    <h4>选项二</h4>
    <Minput
      type='text'
      text='尺寸'
      v-model="pizza.options[1].size"
    ></Minput>
    <Minput
      type='text'
      text='价格'
      v-model="pizza.options[1].price"
    ></Minput>
    <div class="input">
      <button @click='handle'>添加</button>
    </div>
  </div>
</template>

<script>
import Minput from "./commen/Minput";
export default {
  data() {
    return {
      pizza: {
        name: "",
        description: "",
        options: [
          {
            size: "",
            price: ""
          },
          {
            size: "",
            price: ""
          }
        ]
      }
    };
  },
  components: {
    Minput
  },
  methods: {
    handle() {
      console.log(this.pizza);
      this.$axios
        .post("/menu.json", this.pizza)
        .then(res => {
          console.log(res);
          this.$emit("update");
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.input {
  width: 100%;
}
.input button {
  border: 0;
  border-color: #28a745;
  background-color: #28a745;
  height: 36px;
  border-radius: 5px;
  padding: 6px 12px;
  width: 100%;
  color: #fff;
  margin-top: 20px;
}
</style>