<template>

  <div class="form-group">
    <label for="exampleInputEmail1">{{text}}</label>
    <input
      :type="type"
      class="form-control"
      :value='value'
      @input="$emit('input',$event.target.value)"
    >
  </div>
</template>

<script>
export default {
  props: {
    text: String,
    type: String,
    value: String
  }
};
</script>

<style scoped>
.input {
  width: 100%;
}
.input input {
  width: 98%;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 6px 12px;
  height: 26px;
  margin-top: 6px;
}
</style>